el/models\";\nimport { IRouter } from \"../../../../../domain/presentation/IRouter\";\n\ninterface IDialogProps {\n    parentId: string;\n    processItems: Array<IModel>;\n}\n\ninterface IUserOptions { name: string, id: string }; // id or value ? \n\nconst props = defineProps<IDialogProps>();\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst router = container.resolve<IRouter>(IRouter);\n\nconst emit = defineEmits([\"close\"]);\n\nconst usernameInput = ref<string>(\"\");\nconst processInput = ref<string>(\"\");\nconst listUsers = ref<Array<IUser>>([]);\nconst userOptions = ref<Array<IUserOptions>>([]);\nconst processOptions = ref<Array<string>>([]);\nconst name = ref(\"\");\nconst processID = ref<string>(\"\");\n\nconst workflow = window.workflow;\nconst disableButton = computed(()=> processInput.value.length > 0) \n\nonMounted(async () => {\n        executor.execute(\n        async () => {\n            listUsers.value = await workflow.listUsers();\n            convertUserOptions();\n            convertProcesses();\n        },\n        { loading: true }\n    );\n});\nfunction convertUserOptions() {\n    let options: Array<IUserOptions> = [];\n    listUsers.value.forEach((item: IUser) => {\n        options.push({ name: item.name, id: item.id });\n    })\n    userOptions.value = options;\n}\n\nfunction selectUserName(value: string) {\n    usernameInput.value = value;\n    const userInfo = listUsers.value.find((user)=> user.id == value) as IUser;\n    workflow.setUser(userInfo!);\n}\nfunction convertProcesses() {\n    processOptions.value = props.processItems.map((processModel) => processModel.name);\n}\nfunction selectProcess(value: string) {\n    processInput.value = value;\n    processID.value = props.processItems.find((model) => model.name == value )!.ID;\n}\n\nfunction create() {\n    executor.execute(\n        async () => {\n            workflow.setProcessToViewModel(processID.value);\n            workflow.setIsNewProcess(true);\n   